
/* this css matches that in section-container and section-header */
.api-header {
	display: grid;
  font-size: 1em;
  grid-template-columns: minmax(auto, 15%) 35% 50% 0px;
  column-gap: 1.5em;
  align-items: center;
  grid-template-areas:
    ". header code code";
}

.api-header .code-block {
  margin-top: 0em;
  padding-top: 2em;
  padding-bottom: 2em;
}

/* mobile */
@media only screen and (max-width: 700px) {
  .api-header {
    grid-template-columns: auto 1fr auto;
    grid-template-areas:
    ".    header ."
    "code code   code";
  }
}
